import React, { useState } from "react";
import './Login.scss';
import logo from '../../assets/images/blackbox-ip-logo.png';
import * as Yup from "yup";
import { useFormik } from 'formik';
import { useHistory } from "react-router-dom";
import { save } from "../../shared/APIHandler";
import { API_URLS } from "../../shared/Constants";
import { setContext } from "../../shared/SessionHelper";
import { toast } from 'react-toastify';
// import BlockUi from 'react-block-ui';
// import 'react-block-ui/style.css';



function Login() {

  const [toggleBlock, setToggleBlock] = useState(false);
  const history = useHistory();

  // Formik
  const formik = useFormik({
    initialValues: {
      userName: "",
      password: ""
    },
    validationSchema: Yup.object().shape({
      userName: Yup.string().required("Please enter user name."),
      password: Yup.string().required("Please enter password.")
    }),
    onSubmit: (values) => {
      // Integrate Login API
      const params = {
        "username": values.userName,
        "password": values.password
      };
      setToggleBlock(true);
      // POST API execution
      save(API_URLS.login, params, (error, response) => {
        if (response && response.data) {
          console.log("response.data: ", response.data);
          setContext({token: response.data.accessToken, userName: values.userName});
          setToggleBlock(false);
          history.push("/bbx/home");
        } else {
          toast.error("Invalid User Name or Password.");
          setToggleBlock(false);
          console.log("error: ", error);
        }
      });
    },
  });

  return (
    // <BlockUi tag="div" blocking={toggleBlock}>
      <div className="login-container">
        <h2>Sign In</h2>
        <div className="login-logo">
          <img className="logo" src={logo} alt="IDS logo" />
        </div>

        <form onSubmit={formik.handleSubmit}>
          <div className="form-horizontal">
            <div className="form-group">
              <label className="control-label">Username</label>
              <input
                type="text"
                className="form-control"
                name="userName"
                onChange={(e) => {
                  formik.setFieldValue(
                    "userName",
                    e.target.value,
                  );
                }}
              />
              {formik.errors.userName && <div id="feedback">{formik.errors.userName}</div>}
            </div>
            <div className="form-group mt-3">
              <label className="control-label">Password</label>
              <input
                name="password"
                type="password"
                className="form-control"
                onChange={(e) => {
                  formik.setFieldValue(
                    "password",
                    e.target.value,
                  );
                }}
                />
              {formik.errors.password && <div id="feedback">{formik.errors.password}</div>}
            </div>
            <div className="form-group mt-4">
              <button
                type="submit"
                className="btn btn-primary col-12"
                disabled={!formik.isValid || toggleBlock}
                >
                Login
                </button>
            </div>
          </div>
        </form>
        <a href="forgotPassword" className="forgot-pass-link mt-2">Forgot Password?</a>
      </div>
    // </BlockUi>
  );
}

export default Login;
