
import Highcharts from "highcharts";
import ReactHighcharts from "highcharts-react-official";

function ColumnChart({ chartData }) {

	const optionsBar = {
		chart: {
			type: "column",
			height: 250
		},
		title: {
			text: ""
		},
		xAxis: {
			categories: chartData.categories
		},
		yAxis: {
			title: {
				text: ""
			},
			visible: false
		},
		tooltip: {
			shared: true,
		},
		credits: {
			enabled: false
		},
		plotOptions: {
			column: {
				dataLabels: {
					enabled: true,
					format: `{y}${chartData.formatUnit ? chartData.formatUnit :""}`
				}
			}
		},
		legend: {
			enabled: false
		},
		series: chartData.series
	};

	return (
		<ReactHighcharts
			ref={chartData.ref}
			highcharts={Highcharts}
			options={optionsBar}
			domProps={{ id: chartData.id }}
		/>
	);
}

export default ColumnChart;
