
import Highcharts from "highcharts";
import ReactHighcharts from "highcharts-react-official";

function PieChart({ chartData }) {

	const optionsPie = {
		chart: {
			type: "pie",
			height: 300
		},
		title: {
			text: ""
		},
		tooltip: {
			shared: true,
		},
		credits: {
			enabled: false
		},
		plotOptions: {
			pie: {
				borderWidth: 0,
				dataLabels: {
					enabled: true,
					connectorWidth: 0,
					distance: -15,
					formatter() {
						return `${this.y}%`;
					},
					inside: true
				}
			}
		},
		legend: {
			enabled: true,
			layout:'horizontal'
		},
		series: chartData.series
	};

	return (
		<ReactHighcharts
			ref={chartData.ref}
			highcharts={Highcharts}
			options={optionsPie}
			domProps={{ id: chartData.id }}
		/>
	);
}

export default PieChart;
