
import Highcharts from "highcharts";
import ReactHighcharts from "highcharts-react-official";

function StackedChart({ chartData }) {

	const optionsStacked = {
		chart: {
			type: "column",
			height: 250
		},
		title: {
			text: ""
		},
		xAxis: {
			categories: chartData.categories
		},
		yAxis: {
			title: {
				text: ""
			}
		},
		tooltip: {
			shared: true,
		},
		credits: {
			enabled: false
		},
		plotOptions: {
			column: {
				stacking: "normal",
			}
		},
		legend: {
			enabled: true
		},
		series: chartData.series
	};

	return (
		<ReactHighcharts
			ref={chartData.ref}
			highcharts={Highcharts}
			options={optionsStacked}
			domProps={{ id: chartData.id }}
		/>
	);
}

export default StackedChart;
