
import Highcharts from "highcharts";
import ReactHighcharts from "highcharts-react-official";

function SemiCircleChart({ chartData }) {
	const optionsSemi = {
		chart: {
			height: 250
		},
		title: {
			text: `<div style="font-size: 14px; position: absolute; margin-top: 5px; margin-left: -8px"> ${chartData.title}</div>`,
			align: 'left',
			useHTML: true
		},
		subtitle: {
			text: `<b><span style="font-size: 16px;">${chartData.subtitle}</span></b>`,
			align: 'center',
			verticalAlign: 'middle',
			y: 75
		},
		tooltip: {
			formatter: function () {
				if (this.key === 'disabled') {
					return false;
				}
				return '<b>' + (this.y) + '%</b>';
			}
		},
		plotOptions: {
			pie: {
				dataLabels: {
					enabled: false
				},
				startAngle: -90,
				endAngle: 90,
				center: ["50%", "85%"],
				size: "120%",
			}
		},
		colors: ["#6197e2", "#d8d8d8"],
		series: [
			{
				innerSize: "75%",
				type: "pie",
				data: chartData.data
			}
		],
		credits: {
			enabled: false
		},
		legend: {
			enabled: false
		},
	};


	return (
		<ReactHighcharts
			ref={chartData.ref}
			highcharts={Highcharts}
			options={optionsSemi}
			domProps={{ id: chartData.id }}
		/>
	);
}

export default SemiCircleChart;
