
import './Header.scss';
import logo from '../../assets/images/blackbox-ip-logo.png';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Popover from 'react-bootstrap/Popover';
import { Navbar, Nav, Container } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCaretDown } from '@fortawesome/fontawesome-free-solid';
import { useHistory, useLocation } from "react-router-dom";
import {removeContext, getUserName} from '../../shared/SessionHelper';

function Header() {

	const history = useHistory();
	const location = useLocation()

	const logout = () => {
		removeContext();
		history.push("/login");
	}

	const setActiveClass = (selectedTab) => {
		return location.pathname.includes(selectedTab) ? "active" : "";
	}

	const popover = (
		<Popover id="popover-basic">
			<Popover.Body>
				<div className="row">
					{/* <div className="col-12">
						<button type="button" class="btn btn-light">My Profile</button>
					</div> */}
					<div className="col-12">
						<button type="button" class="btn btn-light btn-lg" onClick={logout}>Logout</button>
					</div>
				</div>
			</Popover.Body>
		</Popover>
	);

	return (
		<div className="header-container">
			<div className="container top-header">
				<img className="logo" src={logo} alt="IDS logo" />
				<OverlayTrigger trigger="click" rootClose placement="bottom-end" overlay={popover}>
					<div className="login-user">
						<span>
							{getUserName().replace(/[^a-zA-Z ]/g, "")}
							<span>
								<FontAwesomeIcon icon={faCaretDown} className="caret-icon" />
							</span>
						</span>
					</div>
				</OverlayTrigger>
			</div>


			<div className="container-fluid mt-2 p-0">
				<Navbar expand="lg">
					<Container>
						<Navbar.Toggle aria-controls="basic-navbar-nav" />
						<Navbar.Collapse id="basic-navbar-nav">
							<Nav className="me-auto nav-item">
								<Nav.Link href="/bbx/home" className={setActiveClass('home')}>Home</Nav.Link>
								<Nav.Link href="/bbx/art-unit" className={setActiveClass('art-unit')}>Art unit report</Nav.Link>
								<Nav.Link href="/bbx/examiner" className={setActiveClass('examiner')}>Examiner report</Nav.Link>
							</Nav>
						</Navbar.Collapse>
					</Container>
				</Navbar>
			</div>
		</div>
	);
}

export default Header;
