	const loadArtUnits = () => {
		setArtUnitsLoader(true);
		get(API_URLS.artUnitList, (error, response) => {
			if(response && response.data) {
				const unitList = [];
				const { ArtUnit } = response.data;
				for (let index in ArtUnit) {
					unitList.push({ "value": ArtUnit[index], "label": ArtUnit[index] })
				}
				setArtUnits(unitList);
				setArtUnitsLoader(false);
				if (unitList.length > 0) {
					setSelectedArtUnit(unitList[0]);
				}

			} else {
				setArtUnits([]);
				setArtUnitsLoader(false);
			}
		});
	}

