// imports
import axios from "axios";
import * as sessionHandler from "./SessionHelper";
import { API_URLS } from "./Constants";

/**
 * We are using get for GET method, save for POST method, update for PUT method, remove for DELETE method.
 * As delete is keyword we are using method name as remove.
 */

// module.exports = {

/**
 * Executes GET API
 * @param url - Input URL for GET method.
 * @returns {Axios Promise}
 */
export function get(url, callback) {
	headers();
	return axios
		.get(url)
		.then(function (response) {
			callback(null, response);
		})
		.catch(function (error) {
			handleError(error);
			callback(error, null);
		});
}

/**
 * Executes POST API.
 * @param url - Input URL for POST method.
 * @param data - Data to be passed over the POST call.
 * @returns {Axios Promise}
 */
export function save(url, data, callback) {
	headers();
	return axios
		.post(url, data)
		.then(function (response) {
			callback(null, response);
		})
		.catch(function (error) {
			handleError(error);
			callback(error, null);
		});
}

/**
 * Executes POST API.
 * @param url - Input URL for POST method.
 * @param data - Data to be passed over the POST call.
 * @returns {Axios Promise}
 */
export function saveWithHeader(url, data, headerParams, callback) {
	headers();

	if (headerParams) {
		for (let key in headerParams) {
			axios.defaults.headers.common[key] = headerParams[key];
		}
	}

	return axios
		.post(url, data)
		.then(function (response) {
			callback(null, response);
		})
		.catch(function (error) {
			handleError(error);
			callback(error, null);
		});
}

/**
 * Executes PUT API.
 * @param url - Input URL for PUT method.
 * @param data - Data to be passed over the PUT call.
 * @returns {Axios Promise}
 */
export function update(url, data, callback) {
	headers();
	return axios
		.put(url, data)
		.then(function (response) {
			callback(null, response);
		})
		.catch(function (error) {
			handleError(error);
			callback(error, null);
		});
}

/**
 * Executes DELETE API
 * @param url - nput URL for DELETE method.
 * @returns {Axios Promise}
 */
export function remove(url, callback) {
	return axios
		.delete(url)
		.then(function (response) {
			callback(null, response);
		})
		.catch(function (error) {
			handleError(error);
			callback(error, null);
		});
}
