/**
 * Executes POST API.
 * @param url - Input URL for POST method.
 * @param data - Data to be passed over the POST call.
 * @returns {Axios Promise}
 */
export function saveWithFiles(url, data, callback) {
	headers();

	let formData = new FormData();

	if (data) {
		for (let key in data) {
			if (key === "image") {
				formData.append(key, data[key], data[key].name);
			} else {
				formData.append(key, data[key]);
			}
		}
	}

	return axios
		.post(url, formData)
		.then(function (response) {
			callback(null, response);
		})
		.catch(function (error) {
			handleError(error);
			callback(error, null);
		});
}

var headers = function () {
	axios.defaults.headers.post["Content-Type"] = "application/json";
	axios.defaults.withCredentials = false;
	axios.defaults.headers.common["Accept"] = "application/json";

	if (sessionHandler.getToken()) {
		axios.defaults.headers["Authorization"] = `Bearer ${sessionHandler.getToken()}`;
	}
};

var handleError = function (error) {
	if (error && error.response) {
		if (error.response.status === 401) {
			sessionHandler.removeContext();
			window.location.href = '/login';
		}
	}
};
