
import Highcharts from "highcharts";
import ReactHighcharts from "highcharts-react-official";

function LineChart({ chartData }) {

	const optionsLine = {
		chart: {
			type: "spline",
			height: 250
		},
		title: {
			text: ""
		},
		xAxis: {
			categories: chartData.categories
		},
		yAxis: {
			visible: false
		},
		credits: {
			enabled: false
		},
		legend: {
			enabled: chartData.legend
		},
		series: chartData.series,
		plotOptions: {
			series: {
				dataLabels: {
					enabled: true,
					format: '{y}%'
				}
			}
		}
	};

	return (
		<ReactHighcharts
			ref={chartData.ref}
			highcharts={Highcharts}
			options={optionsLine}
			domProps={{ id: chartData.id }}
		/>
	);
}

export default LineChart;
